import React from 'react'
import { Container, Row, Col, Button } from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import './index.css'

const AboutPlasma = () => (
  <div className="About-Suite-Section">
  <div className="background-container">
      <div className="custom-shape custom-shape-1" />
    </div>
  <div className="container p-3 p-md-5 pt-0 pb-0 main-content">
    <div className="" style={{position: 'relative', overflow: 'hidden'}}>
      <Container>
        <Row className="show-grid pt-40 pb-40">
            
            <Col  md={4}>
            <img src="/images/img1.png" width="100%" height="100%" alt="" />
            </Col>
            <Col  md={8}>
            <h1 className="display-3 headline" style={{ color: '#001ada', marginBottom: '0px', marginTop: '20px', fontWeight: 'bold' }}>
                <FormattedMessage id="AboutAresSuite.title" />
                </h1>
            <p style={{ color: '#001ada', fontSize: '1rem' }}>
              <FormattedMessage id="introduce.title" />
            </p>
            <div className="button-maintain-more">
                <Button className="button-2">
                <FormattedMessage id="morevideos.title" />
                </Button>
            </div>
            </Col>
        </Row>
    </Container>
    </div>
  </div>
  </div>
)

export default AboutPlasma



