import React from 'react'
import { Container, Row, Col, Button } from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import './index.css'

const FormatIntlLink = ({ id, link }) => (
  <FormattedMessage id={id}>
    {name => (
      <a href={link} rel="noopener noreferrer" target="_blank" style={{ color: '#ce38da' }}>
        {name}
      </a>
    )}
  </FormattedMessage>
)

const AresCreators = () => (
  <div className="container p-3 p-md-5 main-content mt-80">
    <div className="Ares-Suite-SDK-container" style={{position: 'relative', overflow: 'hidden'}}>
      <Container>
        <Row className="show-grid pt-40 pb-40 pr-25 pl-25">
          <Col  md={8}>
            <h1 className="display-3 headline" style={{ color: '#fff', fontWeight: 'bold' }}>
              <FormattedMessage id="AresCreators.title" />
            </h1>
            <p className="text-white">Provide creators with all-in-one development solution for efficient and <br/>friendly cross-chain collaboration.</p>
          </Col>
          <Col  md={4}>
            <div className="button-maintain-suite">
              <Button className="button-1">
                <FormattedMessage id="get-started.title" />
              </Button>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  </div>
)

export default AresCreators



