import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import '../style/custom.css'
import config from '../../data/SiteConfig'
import IntlProvider from '../util/intlProvider'
import MainLayout from '../util/mainLayout'
import NavBar from '../components/Navbar'
import TechnicalCapabilities from '../components/TechnicalCapabilities'
// import AresRoadMap from '../components/AresRoadMap'
import PartnersAresSuite from '../components/PartnersAresSuite'
import AresCreators from '../components/AresCreators'
import AboutSuitePlasma from '../components/AboutSuitePlasma'
import AboutAresSuite from '../components/AboutAresSuite'
import AresSuiteDevelopers from '../components/AresSuiteDevelopers'
import FooterSuitePage from '../components/FooterSuitePage/Footer'
 
const Section = ({ children, className }) => <div className={className}>{children}</div>
 
const Main = ({ langKey, messages }) => (
  <IntlProvider langKey={langKey} messages={messages}>
    <div>
      <NavBar langKey={langKey} color="Sara" />
      <MainLayout langKey={langKey} theme="mainsuite" footerTheme="footer">
        <Section>
          <AresCreators />
        </Section>
        <Section>
          <AboutSuitePlasma />
        </Section>
        <Section>
          <TechnicalCapabilities />
        </Section>
        <Section>
          <AboutAresSuite />
        </Section>
        <Section>
          <AresSuiteDevelopers />
        </Section>
        <Section>
          <PartnersAresSuite/>
        </Section>
        {/* <Section>
          <AresRoadMap />
        </Section> */}
        <FooterSuitePage config={config} langKey={langKey} />
      </MainLayout>
    </div>
  </IntlProvider>
)
 
export default Main


