import React from 'react'
import { Container, Row, Col, Button } from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import './index.css'

const FormatIntlLink = ({ id, link }) => (
  <FormattedMessage id={id}>
    {name => (
      <a href={link} rel="noopener noreferrer" target="_blank" style={{ color: '#ce38da' }}>
        {name}
      </a>
    )}
  </FormattedMessage>
)

const AboutPlasma = () => (
  <div className="container p-3 p-md-5 pt-0 pb-0 main-content">
    <div className="About-Plasma-container" style={{position: 'relative', overflow: 'hidden'}}>
      <Container>
        <Row className="show-grid pt-40 pb-40">
            <Col  md={6}>
            <div> 
              <div className="icon-left">
                <img className="navbar-logo" src="/images/icon/group-3.svg" alt="ARES" /> 
              </div>
              <div className="text">
                <span className="tagline" style={{color : '#001ada'}}>Hard to start <b>Usability</b></span>
              </div>
            </div>
            <div> 
              <div className="icon-left">
                <img className="navbar-logo" src="/images/icon/group-7.svg" alt="ARES" /> 
              </div>
              <div className="text">
                <span className="tagline" style={{color : '#00d0ff'}}>Unfriendly environment <b>Integration</b></span>
              </div>
            </div>
            <div> 
              <div className="icon-left">
                <img className="navbar-logo" src="/images/icon/group-6.svg" alt="ARES" /> 
              </div>
              <div className="text">
                <span className="tagline" style={{color : '#001ada'}}>Centralised Storage Safety</span>
              </div>
            </div>
            <div> 
              <div className="icon-left">
                <img className="navbar-logo" src="/images/icon/group-10.svg" alt="ARES" /> 
              </div>
              <div className="text">
                <span className="tagline" style={{color : '#00d0ff'}}>Blackbox for game rules Fairness</span>
              </div>
            </div>
            <div> 
              <div className="icon-left">
                <img className="navbar-logo" src="/images/icon/group-16.svg" alt="ARES" /> 
              </div>
              <div className="text">
                <span className="tagline" style={{color : '#001ada'}}>Scalability</span>
              </div>
            </div>
            </Col>
            <Col  md={6}>
            <div className="container main-content">
              <div className="Ares-Suite-SDK-container" >
                <Container>
                  <Row className="show-grid pt-40 pb-40 pr-25 pl-25">
                    <Col  md={12}>
                      <p className="text-white"><b>Plasma</b> is a proposed framework for incentivized and enforced execution of 
                        smart contracts which is scalable to a significant amount of state updates per second 
                        (potentially billions) enabling the blockchain to be able to represent a significant amount of 
                        decentralized financial applications worldwide
                      </p>
                      <div className="">
                        <Button className="button-5">
                        Read the plasma whitepaper
                        </Button>
                      </div>
                    </Col>
                  </Row>
                </Container>
              </div>
            </div>
          </Col>
        </Row>
    </Container>
    </div>
  </div>
)

export default AboutPlasma



