import React from 'react'
import { Container, Row, Col } from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import { chunk } from 'lodash'
import pictures from './pictures'
import ImageTeaser from '../ImageTeaser'

const AresSuiteDevelopers = () => (
  <div className="container p-3 p-md-5  main-content show-grid pt-40 pb-0">
    <div className="About-Plasma-container" style={{position: 'relative', overflow: 'hidden'}}>
      <Container>
        <Row className="">
            <Col  md={12}>
                <h1 className="display-3 headline" style={{ color: '#001ada', fontWeight: 'bold' }}>
                <FormattedMessage id="AresSuiteDevelopers.title" />
                </h1>
            </Col>
        </Row>
        {chunk(pictures, 3).map((row, index) => (
        // eslint-disable-next-line
        <Row key={index}>
          {row.map(pic => (
            <ImageTeaser
              // key={pic.src}
              src={pic.src}
              alt={pic.title}
              link={pic.link}
            />
          ))}
        </Row>
      ))}
    </Container>
    </div>
  </div>
)

export default AresSuiteDevelopers



